html {
  /*cursor: none;*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
a {
  /*cursor: none;*/
}
img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

/*-------- fonts --------*/
@font-face {
  font-family: "News Gothic MT Bold";
  src: url("assets/fonts/NWGTHCB.ttf");
}
@font-face {
  font-family: "Century Gothic Bold";
  src: url("assets/fonts/CenturyGothicBold.ttf");
}
@font-face {
  font-family: "Berthold City Bold";
  src: url("assets/fonts/BertholdCityBold.otf");
}

/*-------- common styles --------*/
body {
  background-color: white;
}
span.blue {
  color: #24135F;
}
span.red {
  color: #DA291C;
}
span.yellow {
  color: #FFCE00;
}
span.white {
  color: #FFFFFF;
}
#app {
  letter-spacing: 1px;
  font-family: "News Gothic MT Bold";
  background-color: white;
  color: #24135F;
  /*width: 1920px;
  height: 1080px;*/
  overflow: hidden;
}
#gesture-swipe-left-icon svg {
  -webkit-filter: drop-shadow( 5px 2px 2px rgba(0, 0, 0, 0.2));
          filter: drop-shadow( 5px 2px 2px rgba(0, 0, 0, 0.2));
}
#gesture-swipe-right-icon svg {
  -webkit-filter: drop-shadow( 5px 2px 2px rgba(0, 0, 0, 0.2));
          filter: drop-shadow( 5px 2px 2px rgba(0, 0, 0, 0.2));
}

/* .material-design-icon {
  display: inline-flex;
  align-self: center;
  position: relative;
  height: 1em;
  width: 1em;
} */
.material-design-icon {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-item-align: center;
      align-self: center;
  position: absolute;
  height: 1em;
  width: 1em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.material-design-icon > .material-design-icon__svg {
  height: 1em;
  width: 1em;
  fill: currentColor;
  position: absolute;
  /* bottom: -0.125em; */
}



